<template>
    <div class="overflow-auto ">
      <b-pagination-nav :link-gen="linkGen" :number-of-pages="10" class="text-warning" use-router></b-pagination-nav>
    </div>
  </template>
  
  <script>
    export default {
      methods: {
        linkGen(pageNum) {
          return pageNum === 1 ? '?' : `?page=${pageNum}`
        }
      }
    }
  </script>

  <style>
    .page-item.active .page-link {
  z-index: 3;
  color: #fff;
  background-color: #ffca2d;
  border-color: #ffca2d;
}
.page-link:hover {
  z-index: 2;
  color: #ffca2d;
  text-decoration: none;
  background-color: #e9ecef;
  border-color: #dee2e6;
  
}.page-link{

  color: #ffca2d;

}
</style>