<template>
    <div>
      <b-form @submit="onSubmit" @reset="onReset" v-if="show">
        <b-form-group id="input-group-1" label="El teu nom:" label-for="input-1">
          <b-form-input
            id="input-2"
            v-model="form.name"
            placeholder="Inserta el nom"
            required
          ></b-form-input>
        </b-form-group>

        <b-form-group
          id="input-group-2"
          label="Correu electrònic:"
          label-for="input-2"
        >
          <b-form-input
            id="input-2"
            v-model="form.email"
            type="email"
            placeholder="Inserta el correu"
            required
          ></b-form-input>
        </b-form-group>
  
     

        <b-form-group id="input-group-3" label="Explica la teva història:" label-for="input-3">
         <b-form-textarea
            id="textarea"
            v-model="text"
            placeholder="Inserta el text"
            rows="3"
            max-rows="6"
         ></b-form-textarea>
        </b-form-group>

        <b-form-group id="input-group-4" label="Exporta la teva imatge:" label-for="input-4">
            <b-form-file
             v-model="file1"
             :state="Boolean(file1)"
             placeholder="Escull un fitxer o deixa-ho aqui"
             drop-placeholder="Drop file here..."
            ></b-form-file>
        <div class="mt-3">Arxiu seleccionat: {{ file1 ? file1.name : '' }}</div>
            <b-form-file v-model="file2" class="mt-3" plain></b-form-file>
        <div class="mt-3">Arxiu seleccionat: {{ file2 ? file2.name : '' }}</div>
        </b-form-group>
        <b-button type="submit" variant="warning">Envia</b-button>
      </b-form>
    </div>
  </template>
  
  <script>
    export default {
      data() {
        return {
          form: {
            email: '',
            name: '',
            food: null,
            checked: [],
            text: '',
            file1: null,
            file2: null
          },
          foods: [{ text: 'Select One', value: null }, 'Carrots', 'Beans', 'Tomatoes', 'Corn'],
          show: true
        }
      },
      methods: {
        onSubmit(event) {
          event.preventDefault()
          alert(JSON.stringify(this.form))
        },
        onReset(event) {
          event.preventDefault()
          // Reset our form values
          this.form.email = ''
          this.form.name = ''
          this.form.food = null
          this.form.checked = []
          // Trick to reset/clear native browser form validation state
          this.show = false
          this.$nextTick(() => {
            this.show = true
          })
        }
      }
    }
  </script>

